@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/open-sans-light.woff2") format("woff2"), url("../fonts/open-sans-light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/open-sans-semibold.woff2") format("woff2"), url("../fonts/open-sans-semibold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/open-sans-bold.woff2") format("woff2"), url("../fonts/open-sans-bold.woff") format("woff"); }

body {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  margin: 0 auto;
  color: #1b2d37;
  font-family: "Open Sans", "Arial", sans-serif;
  overflow-x: hidden; }

body,
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }
